export const RECEIVED_SNACKBAR_MESSAGE = 'RECEIVED_SNACKBAR_MESSAGE';
export const CLEAR_SNACKBAR = 'CLEAR_SNACKBAR';
export const DEFAULT_SNACKBAR = {
  message: null,
  duration: null
};
export default (function () {
  let state = arguments.length > 0 && arguments[0] !== undefined ? arguments[0] : DEFAULT_SNACKBAR;
  let action = arguments.length > 1 ? arguments[1] : undefined;

  switch (action.type) {
    case RECEIVED_SNACKBAR_MESSAGE:
      {
        return { ...state,
          ...action.value
        };
      }

    case CLEAR_SNACKBAR:
      {
        return DEFAULT_SNACKBAR;
      }

    default:
      return state;
  }
}); // Selectors

export const sGetSnackbar = state => state.snackbar;