import { getInstance } from 'd2';
import { onError } from './index.js';
export const NAMESPACE = 'analytics';
export const CURRENT_AO_KEY = 'currentAnalyticalObject';
export const hasNamespace = async d2 => await d2.currentUser.dataStore.has(NAMESPACE);
export const getNamespace = async (d2, hasNamespace) => hasNamespace ? await d2.currentUser.dataStore.get(NAMESPACE) : await d2.currentUser.dataStore.create(NAMESPACE);
export const apiSave = async (data, key, namespace) => {
  try {
    const d2 = await getInstance();
    const ns = namespace || (await getNamespace(d2, await hasNamespace(d2)));
    return ns.set(key, data);
  } catch (error) {
    return onError(error);
  }
};
export const apiFetch = async (key, namespace) => {
  try {
    const d2 = await getInstance();
    const ns = namespace || (await getNamespace(d2, await hasNamespace(d2)));
    return ns.get(key);
  } catch (error) {
    return onError(error);
  }
};
export const apiSaveAOInUserDataStore = function (current) {
  let key = arguments.length > 1 && arguments[1] !== undefined ? arguments[1] : CURRENT_AO_KEY;
  return apiSave(current, key);
};
export const apiFetchAOFromUserDataStore = function () {
  let key = arguments.length > 0 && arguments[0] !== undefined ? arguments[0] : CURRENT_AO_KEY;
  return apiFetch(key);
};